<template>
<transition name="slide-fade">
	<div class="cc-main">
		<div class="cc-register">
			<div class="cc-tit">手机快捷登录</div>
			<van-field class="cc-input" v-model="tel" type="tel" center clearable  placeholder="请输入手机号">
				<van-button v-show="show" slot="button" size="small" type="primary" @click="sent">发送验证码</van-button>
				<van-button v-show="!show" slot="button" size="small" type="default">倒计时{{count}}</van-button>
			</van-field>
			<van-field class="cc-input" v-model="sms"  type="number" placeholder="请输入验证码" />
			<div class="cc-button" @click="sub">登录</div>
		</div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Field,Toast} from 'vant';
export default {
	name: 'login',
	components: {
		[Field.name]: Field,
		[Toast.name]: Toast,
	},
	data() {
		return {
			tel:"",
			sms:"",
			show: true,
			count: '',
			timer: null,
		};
	},
	mounted() {
	},
	methods: {
		sent(){
			var that=this;
			let test1 = /^1[3-9][0-9]{9}$/
			if (!test1.test(this.tel)) {
				Toast.fail("请填写正确格式的手机号码");
				return false;
			}
			const TIME_COUNT = 60;
			if (!that.timer) {
				that.count = TIME_COUNT;
				that.show = false;
				that.timer = setInterval(() => {
					if (that.count > 0 && that.count <= TIME_COUNT) {
						that.count--;
					} else {
						that.show = true;
						clearInterval(that.timer);
						that.timer = null;
					}
				}, 1000)
			}
			this.$http.post('/common/sendSMS?mobile='+that.tel).then(function (res) {
				if (res.code==10000) {
					Toast.success('短信验证码已发送');
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function (error) {
				Toast.fail(error);
			});
		},
		sub(){
			var that=this;
			this.$http.get(`/main/bindMobile?mobile=${that.tel}&code=${that.sms}`).then(function (res) {
				if (res.code==10000) {
					localStorage.setItem('tgbtoken1',res.data.token);
					var role=that.$route.query.role;
					if (role==1) {
						that.$router.replace({
							path:"/home"
						})
					}else if(role==2){
						if (res.data.isIntermediary==1) {
							that.$router.replace({
								path:"/home01"
							})
						}else{
							that.$router.replace({
								path:"/home1"
							})
						}
					}else if(role==3){
						if (res.data.isCompany==1) {
							that.$router.replace({
								path:"/home2"
							})
						}else{
							that.$router.replace({
								path:"/checkin"
							})
						}
					}else{
						that.$router.replace({
							path:"/role"
						})
					}
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},

	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	padding-top: 21px;
	height: 100vh;
	background:#1E97A8 url(https://quickrequest.oss-cn-beijing.aliyuncs.com/image/347c00a9f5d3148cd36e80a1ec12473.png) left bottom no-repeat;
	background-size: 375px 258px;
	position: relative;
}
.cc-logo1{
	position: fixed;
	left: 0;
	bottom: 0;
	width:375px;
	height:315px;
}
.cc-register{
	width:355px;
	height:284px;
	background:rgba(255,255,255,1);
	box-shadow:0px 4px 12px 0px rgba(37,65,42,0.16);
	border-radius:8px;
	padding:22px 20px 24px;
	margin:0 auto;
	.cc-tit{
		font-size:24px;
		font-weight:600;
		color:rgba(51,51,51,1);
		line-height:33px;
		padding-bottom:22px;
		text-align: center;
	}
	.cc-input{
		padding:10px 0;
		line-height:32px;
		font-size:17px;
	}
	.cc-button{
		width:315px;
		height:47px;
		background:#1E97A8;
		border-radius:2px;
		line-height:47px;
		text-align: center;
		color:#fff;
		font-size:18px;
		font-weight:500;
		margin-top: 30px;
	}
}
.cc-pop{
	width:298px;
	height:207px;
	background:rgba(255,255,255,1);
	border-radius:8px;
	.cc-des{
		height: 50px;
		text-align: center;
		line-height: 50px;
		font-size:14px;
		font-weight:500;
		color:rgba(0,0,0,0.4);
	}
	.cc-button1{
		height:77px;
		padding:25px 0 0 78px;
		border-bottom: 1px dashed #eee;
		img{
			width: 28px;
			height: 28px;
			margin-right: 15px;
		}
		span{
			font-size:16px;
			font-weight:500;
			color:rgba(0,0,0,0.8);
			line-height:28px;
		}
	}
}
</style>